<template>
  <div>
    <h1>DevBlog</h1>
    <b-card
      v-for="post in posts"
      :key="post.id"
      :title="post.title"
      tag="article"
      style="max-width: 1080px;"
      class="mb-2 mx-auto"
    >
      <b-card-sub-title class="mb-2">Author: {{ post.author }}</b-card-sub-title>
      <b-card-text>
        {{ post.text }}
      </b-card-text>
      <b-card-footer>{{ post.date }}</b-card-footer>
    </b-card>
    <router-link to="/about">About Page</router-link>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      posts: [
        {id: 1, date: "2023-04-19", author: "Marc", title: "Hello world", text: "Irure esse quis cupidatat et quis consectetur veniam pariatur amet pariatur enim ipsum. Sit excepteur nisi consequat esse ullamco pariatur velit proident qui cupidatat sit sint Lorem. Ullamco enim consequat nisi nostrud pariatur irure dolor sunt nulla. Laborum non dolore culpa minim ipsum ex consequat in tempor laborum deserunt. Officia ea id et nulla ullamco. Exercitation exercitation occaecat Lorem aute exercitation aute velit pariatur quis. Elit cillum pariatur ipsum pariatur dolore amet nostrud ullamco dolore."},
        {id: 2, date: "2023-04-22", author: "Bob", title: "A new world", text: "Aute duis minim velit id nulla est mollit veniam esse. Voluptate cupidatat ad dolor esse sunt do sunt exercitation veniam. Sit ea sint cupidatat fugiat incididunt dolore adipisicing do commodo. Sit magna occaecat minim ipsum pariatur qui excepteur. Cillum sit laboris do nisi eu incididunt aliquip."},
      ],
    }
  },
  async created() {
    try {
      let response = await fetch("blabla");
      let data = await response.json();
      posts = data;
    } catch(e) {
      this.error(e.message)
    }
  },
  methods: {
    error(msg) {
      this.$bvToast.toast(
        msg, 
        {
          title: "Error",
          variant: "danger",
          solid: true
        }
      )
    }
  }
}
</script>

<style scoped></style>


